@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Share+Tech+Mono&display=swap");

$default-font-size: 1.25rem;
$red: #c80405;
$blue: #2684e6;

body {
  margin: 0;
}
.app {
  font-family: "Share Tech Mono", monospace;
  font-size: $default-font-size;
  padding: 40px 40px 60px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1,
  h2 {
    font-family: "Press Start 2P", monospace;
  }
  h1 {
    font-size: 2.5rem;
    line-height: 3.5rem;
  }
  h2 {
    font-size: 1.5rem;
    line-height: 2.5rem;
  }

  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    text-align: center;

    .logo {
      width: 160px;
      margin-bottom: 10px;
    }
  }

  .leaderboard,
  .countdown {
    margin-bottom: 60px;
  }

  .tableContainer {
    width: 100%;
    overflow: auto;
  }

  table {
    text-align: left;
    border-spacing: 0;
    margin: auto;
    th,
    td {
      padding: 10px 50px 10px 10px;
      border-bottom: 1px solid black;
      &:last-child {
        padding-right: 10px;
      }
      &.points {
        padding-left: 10px;
      }
      &.medal {
        padding-right: 0;
      }
      &.alignCenter {
        text-align: center;
      }
    }
    th {
      white-space: nowrap;
    }
  }

  .leaderboard,
  .challenges {
    max-width: 100%;
  }

  .leaderboard {
    font-size: 1.5rem;
    .playerName {
      display: flex;
      align-items: center;
    }
    .avatar {
      height: 40px;
      border-radius: 100%;
      margin-right: 20px;
    }
    td.small {
      font-size: $default-font-size;
    }
  }

  .challenges {
    tbody tr {
      &:hover:not(.completed) {
        background-color: #fff5ca;
        transition: ease 0.25s;
        font-weight: bold;
      }
      &.completed {
        color: #b2b2b2;
        .icon {
          filter: brightness(0.4);
        }
      }

      td {
        padding-top: 5px;
        padding-bottom: 5px;
        &:first-child {
          padding-right: 10px;
        }
        .icon {
          height: 50px;
          border-radius: 8px;
          display: block;
        }
      }
    }
  }

  .countdown {
    display: flex;
    font-size: 1.75rem;
    text-align: center;

    .timeItem {
      margin-right: 40px;
      &:last-child {
        margin-right: 0;
      }
    }
    .time {
      font-weight: bold;
      font-size: 2.25rem;
      font-family: "Press Start 2P", monospace;
      margin-bottom: 10px;
    }
    .customStatus,
    .time {
      color: $red;
    }
  }
}

.loading {
  color: $blue;
}

@media screen and (max-width: 768px) {
  .app {
    h2 {
      text-align: center;
    }
    .leaderboard {
      font-size: $default-font-size;
    }
    .switchContainer {
      margin-left: auto;
      margin-right: auto;
    }
  }
}
@media screen and (max-width: 576px) {
  .app {
    padding: 20px;
    font-size: 1rem;

    h1 {
      font-size: 2rem;
      line-height: 3rem;
    }
    h2 {
      font-size: 1rem;
      line-height: 2rem;
    }
    .header .logo {
      width: 130px;
    }
    .countdown,
    .leaderboard {
      margin-bottom: 40px;
    }
    .leaderboard {
      font-size: 1rem;
    }
    .countdown {
      font-size: $default-font-size;

      .timeItem {
        margin-right: 20px;
      }
      .time {
        font-size: 1.5rem;
      }
    }
  }
}
